    text-transform: capitalize;
}
.payment-radio input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
}
.payment-radio .checkmark {
    position: absolute;
    top: 0;
    left: 0;
    width: 19px;
    height: 19px;
    margin: 3px 0 0 0;
    border: 2px solid #ddd;
	border-top-color: rgb(221, 221, 221);
	border-right-color: rgb(221, 221, 221);
	border-bottom-color: rgb(221, 221, 221);
	border-left-color: rgb(221, 221, 221);
    border-radius: 50%;
    transition: all .3s;
}
.payment-radio input:checked ~ .checkmark {
    border-color: #1e88e5;
}
.payment-radio .checkmark::after {
    position: absolute;
    left: 3px;
    top: 3px;
    content: '';
    width: 9px;
    height: 9px;
    background-color: #1e88e5;
    opacity: 0;
    visibility: hidden;
    transform: scale(0.1);
    border-radius: 50%;
    transition: all .3s;
}
.payment-radio input:checked ~ .checkmark::after {
    transform: scale(1);
    opacity: 1;
    visibility: visible;
}
