html, body { height: 100%; }
body { margin: 0; font-family: Roboto, "Helvetica Neue", sans-serif; }
/*::-webkit-scrollbar {
  width: 0px;  !* Remove scrollbar space *!
  background: transparent;  !* Optional: just make scrollbar invisible *!
}*/
.primary-color {
  color: #64a19d !important;
}
.primary-background-color {
  background-color: #64a19d !important;
  color: white !important;
}
.primary-lighter-background-color {
  background-color: #84d1cc !important;
  color: #fff !important;
}
.primary-lighter-background-color p {
  color: #fff  !important;
}
.primary-lighter-background-color h4 {
  color: #fff  !important;
}
.custom-gray-color {
  color: #6c757d;
}
.mat-drawer-inner-container::-webkit-scrollbar{
  width: 0 !important;  /* Remove scrollbar space */
  background: transparent !important;
}
.custom-dark-gray-color {
  color: #5b646b;
}
.center-screen {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  min-height: 60vh;
}
.paginationPosition {
  position: absolute;
  bottom: 0;
  left:0;
  right:0;
}
.fontSizes {
  font-weight: bold; font-size: 15px; color: #6c757d;
}
.imgStyle {
  background-color: white;
  object-fit: cover;
  width: 80px;
  cursor: pointer;
  height:80px;
  border-radius: 100px;
  margin-bottom: 20px;
  margin-top: 20px;
}
.fontNavbar {
  font-weight: bold;
  font-size: 25px;
  color: #6c757d;
}
.filledStar {
  color: #febe42 !important;
  padding-right: 5px;
  font-size: 20px;
}
.notFilledStar {
  color: #828282;
  padding-right: 5px;
  font-size: 20px;
}
.example-container {
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-clip: content-box;
  background-size: cover;
  background-image: url('backgroundTruck.png');
}
.example-sidenav-content {
  display: flex;
  flex-direction: column;
  height: 100%;
  align-items: center;
  justify-content: center;
}
.example-sidenav {
  background-color: rgba(255, 255, 255, 0.95);
  min-width: 250px;
  max-width: 350px;
  width: 35%;
}
.example-sidenav-small {
  background-color: rgba(255, 255, 255, 0.95);
  width: 100%;
}
.example-sidenav-full {
  background-color: rgba(255, 255, 255, 0.95);
  width: 100%;
  padding-left: 20%;
  padding-right: 20%;
}
